:root{
    --default-text-color: white;

    --default-font-size: 0.9rem;
    --mobile-font-size: 0.765rem;

    --astr-background-color: #1A1537;
    --astr-background-welcome: rgb(13, 8, 34);
    --astr-background-topics: rgb(5, 3, 15);
    --astr-border-color: #593A99;
    --astr-button-color: linear-gradient(to right,rgba(32,12,59,.9),rgba(32,12,59,.9));
    --astr-button-hover: linear-gradient(to right,rgba(32,12,59,.9), #58398b, rgba(32,12,59,.9));
}